/*// PRIMARY STYLES ----------------------- */
.bdhwk body {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  color: #333333;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
  animation: bodyFade 0.5s forwards;
}
@keyframes bodyFade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.bdhwk main {
  overflow: hidden;
}
.bdhwk ::selection {
  background: #333333;
  color: #ffffff;
}
.bdhwk ::-moz-selection {
  background: #333333;
  color: #ffffff;
}
.bdhwk a {
  text-decoration: none;
  color: #333333;
  webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
.bdhwk h1 {
  color: #333333;
  font: 700 40px/40px "Roboto", sans-serif;
  margin: 0px;
}
.bdhwk h2 {
  color: #333333;
  font: 300 27px/27px "Roboto", sans-serif;
  margin: 0px;
}
.bdhwk h3 {
  color: #333333;
  font: 700 18px/18px "Roboto", sans-serif;
  margin: 0px;
}
.bdhwk h4 {
  color: #333333;
  font: 300 14px/14px "Roboto", sans-serif;
  margin: 0px;
}
.bdhwk h5 {
  color: #333333;
  font: 700 14px/14px "Roboto", sans-serif;
  margin: 0px;
}
.bdhwk h6 {
  color: #333333;
  font: 300 12px/12px "Roboto", sans-serif;
  margin: 0px;
}
.bdhwk p {
  color: #333333;
  font: 300 12px/12px "Roboto", sans-serif;
  margin: 0px;
}
.bdhwk hr {
  border-bottom: 1px solid #333333;
}

/**
 * Basic Blocks
 */
.basic-headline {
  margin: 40px auto 10px;
}
.basic-headline h1, .basic-headline h2, .basic-headline h3, .basic-headline h4, .basic-headline h5, .basic-headline h6 {
  letter-spacing: 1px;
}

.basic-text {
  margin: 10px auto 40px;
}
.basic-text p {
  margin: 10px auto 20px;
}

.basic-wysiwyg a {
  color: black;
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}
.basic-wysiwyg a:hover {
  color: blue;
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}
.basic-wysiwyg ul {
  margin: 30px 0px;
}
.basic-wysiwyg ul li {
  color: black;
  font: 400 16px/24px "Roboto", sans-serif;
  margin: 0px;
}
@media screen and (max-width: 768px) {
  .basic-wysiwyg ul li {
    font: 400 14px/22px "Roboto", sans-serif;
  }
}
.basic-wysiwyg strong {
  font: 700 16px/24px "Roboto", sans-serif;
  color: black;
}
@media screen and (max-width: 768px) {
  .basic-wysiwyg strong {
    font: 700 14px/22px "Roboto", sans-serif;
  }
}

